<template>
    <Card>
        <div style="overflow: hidden;">

            <!--文章主体-->
            <div class="article-main" id="main">
                <h2>
                    文章推荐
                </h2>

                <div class="article-list" v-for="(article, i) in dataList" :key="i">
                    <figure><img :src="imgUrl()" class="layImg"></figure>
                    <ul>
                        <h3>
                            <router-link tag="a" :to="{path:'/cbDetails',query:{msgUrl:article.msgUrl}}">
                                {{article.title}}
                            </router-link>
                        </h3>
                        <p>
                            {{article.description}}
                        </p>
                        <p class="autor">
                            <span class="lm f_l"><a href="https://www.yiibai.com">极客文章</a></span>
                            <span class="dtime f_l">{{article.dateStr}}</span>
                            <span class="dtime f_l">{{article.loveNum}}</span>
                        </p>
                    </ul>
                </div>
                <br>
                <br>
                <!--&lt;!&ndash;分页&ndash;&gt;-->
                <!--<page></page>-->
            </div>
            <!--侧栏-->
            <Side></Side>

        </div>
    </Card>
</template>

<script>
    import {caiBaoList} from '../../../api/data'
    import Side from '../../main/side'

    export default {
        name: "cb_list",
        components: {
            Side
        },
        data() {
            return {
                dataList: [],//数据
                dataCount: 100,//总数
                pageSize: 10,//条数
            }
        },
        created() {
            this.load();
        },
        methods: {
            load() {
                caiBaoList().then(res => {
                    if (res.success === false) {
                        this.error = true;
                        return;
                    }
                    let data = res.data;
                    this.dataList = res.data;
                })
            },
            imgUrl() {//随机图片
                let imgArr = ["01.jpg", "02.jpg", "03.jpg", "04.jpg", "05.jpg", "06.jpg", "1.jpeg", "2.jpeg", "3.jpg", "4.jpg", "5.jpeg"];
                let n = Math.floor(Math.random() * (11 - 1));
                let imgUrl = "/static/img/" + imgArr[n];
                return imgUrl;

            }
        },
    }
</script>

<style scoped>
    @import "../../../assets/styles/layui/global.css";
    /**文章列表 主**/
    .article-main {
        /*margin: 15px 0;*/
        width: 60%;
        float: left;
        margin-left: 8%;
        /*margin-left: 10%;*/

    }

    .layui-col-md4 {
        float: right;
        width: 20%;
        margin-right: 8%;
        margin-top: 24px;
    }

    a {
        color: #333;
        text-decoration: none;
        width: 120px;
        height: 60px;
    }
</style>



